<template>
	<div id="store-setting-more">
		<public-header title="更多设置"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-b">
				<van-cell v-if="store">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/auto_takeOrder.png" alt="">
						</div>
						<div>自动接单</div>
					</div>
					<iswitch :value="store.auto_handel_order" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="store.auto_handel_order" type="auto_handel_order"></iswitch>
				</van-cell>
				<van-cell v-if="store">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/auto_deliveryOrder.png" alt="">
						</div>
						<div>自动通知配送员抢单</div>
					</div>
					<iswitch :value="store.auto_notice_deliveryer" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="store.auto_notice_deliveryer" type="auto_notice_deliveryer"></iswitch>
				</van-cell>
				<van-cell v-if="manager && manager.extra">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/wx_templateNews.png" alt="">
						</div>
						<div>微信模板消息提醒</div>
					</div>
					<iswitch :value="manager.extra.accept_wechat_notice" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="manager.extra.accept_wechat_notice" type="accept_wechat_notice"></iswitch>
				</van-cell>
				<van-cell v-if="manager && manager.extra">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/tel_remind.png" alt="">
						</div>
						<div>语音电话提醒</div>
					</div>
					<iswitch :value="manager.extra.accept_voice_notice" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="manager.extra.accept_voice_notice" type="accept_voice_notice"></iswitch>
				</van-cell>
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	import iswitch from "@/components/switch"
	export default {
		data() {
			return {
				store: {},
				manager: {},
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/shop/index/index',
					success: function(res) {
						that.store = res.store;
						that.manager = res.manager;
					}
				});
			},

			jsToggleSwitch(data) {
				var that = this;
				this.util.jsToggleSwitch({
					vue: that,
					key: data.keys,
					value: data.value,
					url: 'manage/shop/index/status',
					data: {
						type: data.type,
						value: data.value
					}
				});
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-setting-more .content .iswitch{
		height: 24px;
	}
	#store-setting-more .content .van-cell .img{
		width: 25px;
		height: 25px;
		margin-right: 10px;
	}
	#store-setting-more .content .van-cell .img img{
		width: 100%;
		height: 100%;
	}
</style>


